@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,300,600,700,300italic&subset=latin,cyrillic");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

.i-icon-codedby { width: 47px; height: 5px; background-image: url("../img/icons.png"); background-position: -22px -9px; }

.i-icon-coderiver { width: 49px; height: 5px; background-image: url("../img/icons.png"); background-position: -22px 0px; }

.i-icon-mouse { width: 18px; height: 33px; background-image: url("../img/icons.png"); background-position: 0px 0px; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-logo:before { content: ""; }

.icon-phone:before { content: ""; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -10px; font: 0/0 a; text-shadow: none; color: transparent; height: 20px; background: none; left: 0; z-index: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 30px; line-height: 30px; content: "prev"; display: inline-block; vertical-align: middle; font-size: 15px; color: #fff; background: #000; }

.slick-next { right: 0; left: auto; }
.slick-next:before { content: "next"; display: inline-block; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background: #000; }
.slick-dots button { display: block; width: 10px; height: 10px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }

@font-face { font-family: "Zapf Dingbats"; src: url("fonts/Zapf Dingbats.eot"); src: url("fonts/Zapf Dingbats.eot?#iefix") format("embedded-opentype"), url("fonts/Zapf Dingbats.woff2") format("woff2"), url("fonts/Zapf Dingbats.woff") format("woff"), url("fonts/Zapf Dingbats.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { font-family: "Open Sans", sans-serif; font-weight: 400; font-weight: 100; font-size: 18px; line-height: 1.4; background: url("../img/bg.jpg") repeat-y; background-size: cover; }

.out { padding-top: 75px; }

.l-center, .l-center-md, .l-center-sm { max-width: 100%; margin: 0 auto; padding: 0 15px; position: relative; }

.l-center { width: 1630px; }

.l-center-md { width: 1060px; }

.l-center-sm { width: 1005px; }

[class^="i-icon"], [class*=" i-icon-"] { display: inline-block; }

a { text-decoration: none; }

.i-play { display: inline-block; width: 70px; height: 70px; position: relative; border-radius: 50%; background: rgba(28, 22, 52, 0.6); }
.i-play:after { content: ""; position: absolute; width: 0px; height: 0px; border-style: solid; border-width: 14.5px 0 14.5px 23px; border-color: transparent transparent transparent #fff; left: 50%; top: 50%; margin: -13.5px 0 0 -8.5px; }

.btn { line-height: 1; font-size: 18px; color: #fff; background: #e9a84e; padding: 0.72222em 1.61111em; border-radius: 1.2em; font-weight: 100; display: inline-block; text-align: center; font-family: "Open Sans", sans-serif; font-weight: 400; }
.btn_mod { font-size: 12px; font-weight: 600; text-transform: uppercase; color: #202441; }

.hamburger { display: inline-block; vertical-align: middle; cursor: pointer; position: relative; transition: border-color 0.3s; height: 75px; width: 75px; padding: 10px; background: #fff; }
.hamburger span { width: 34px; height: 28px; display: inline-block; border: 3px solid #000; border-width: 3px 0; position: relative; }
.hamburger span:after { content: ""; position: absolute; top: 50%; left: 0; height: 3px; background: #000; width: 100%; margin-top: -1px; transition: background 0.3s; }

.link-more { font-size: 18px; text-decoration: underline; color: #e9a84e; display: inline-block; font-weight: 100; }

.logo-woolim { display: inline-block; width: 255px; height: 103px; background: url("../img/logo-woolim.svg") no-repeat top center; background-size: cover; }
.logo-woolim:before { content: ""; display: inline-block; }

h1, .h1 { color: #e9a84e; font-family: "Open Sans", sans-serif; font-weight: 400; font-weight: 100; font-size: 36px; margin: 1em 0; text-align: center; text-transform: uppercase; }
h1_light, .h1_light { color: #fff; }

h2, .h2 { color: #e9a84e; font-weight: 100; font-size: 24px; text-transform: uppercase; letter-spacing: 1px; }
h2_light, .h2_light { color: #fff; }

h3, .h3 { color: #fff; font-family: "Open Sans", sans-serif; font-weight: 400; font-weight: 100; font-size: 18px; text-transform: uppercase; }

h4, .h4 { color: #e9a84e; font-size: 18px; font-weight: 600; }

.row { *zoom: 1; }
.row:after { content: " "; display: table; clear: both; }

.row-pad { margin: 0 -17px; *zoom: 1; }
.row-pad:after { content: " "; display: table; clear: both; }
.row-pad > div { padding: 0 17px; }

.col4 { float: left; width: 25%; }

.col3 { float: left; width: 33.33333%; }

.col-sm { float: left; width: 31.64179%; }

.col-md { float: left; width: 66.16915%; }

sup { display: inline-block; vertical-align: top; font-size: 0.6em; }

.header { position: fixed; top: 0; left: 0; right: 0; z-index: 100; *zoom: 1; }
.header:after { content: " "; display: table; clear: both; }
.header__in { height: 75px; background: linear-gradient(-96deg, #202642 0%, #201b3b 100%); text-align: center; font-size: 0; position: relative; }
.header__in:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.header__logo { color: #fff; font-size: 50px; line-height: 1; position: absolute; width: 23.125%; text-align: right; left: 0; top: 50%; margin-top: -0.5em; }
.header__btn { display: inline-block; vertical-align: middle; text-align: center; }
.header .callback { position: absolute; right: 0; top: 0; }

.callback { font-size: 20px; }
.callback__link { color: #fff; font-weight: 700; font-size: 24px; margin-right: 15px; line-height: 1; }

.screen { text-align: center; font-size: 0; position: relative; padding-top: 75px; padding-bottom: 70px; margin-top: -75px; height: 100vh; }
.screen:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.screen__bg { position: absolute; left: 0; right: 0; top: 0; bottom: 0; overflow: hidden; }
.screen__bg video { position: absolute; background-repeat: no-repeat; background-size: cover; top: 50%; left: 50%; min-width: 100%; min-height: 100%; transform: translateX(-50%) translateY(-50%); width: auto; height: auto; }
.screen__bg:after { content: ""; position: absolute; left: 0; right: 0; top: 0; bottom: 0; background: rgba(140, 117, 127, 0.3); }
.screen__in { position: relative; z-index: 10; display: inline-block; vertical-align: middle; max-width: 100%; }
.screen__logo { font-size: 200px; color: #fff; }
.screen h1, .screen .h1 { margin: 0.3em 0; }
.screen__section { margin: 0 auto; width: 100%; max-width: 1200px; font-size: 18px; text-align: left; display: table; }
.screen__section .col1 { display: table-cell; width: 50%; position: relative; }
.screen__section .box-video { position: absolute; top: 0; left: 0; right: 0; bottom: 0; }
.screen__icon { margin: 30px 0 25px; text-align: center; position: absolute; left: 0; right: 0; bottom: 0; }

.box { color: #fff; background-image: linear-gradient(-20deg, #180a29 0%, #22324a 50%); font-size: 20px; padding: 2.8em 4.5em 3em 3.5em; }
.box_mod { background: transparent; }
.box h2, .box .h2 { margin-bottom: 30px; }
.box__text { font-weight: 100; letter-spacing: 1px; font-weight: 100; }
.box__text + .link-more { margin-top: 30px; }

.box-video { background-size: cover; background-repeat: no-repeat; text-align: center; font-size: 0; }
.box-video:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.box-video .i-play { display: inline-block; vertical-align: middle; }

.section { background-repeat: no-repeat; background-size: cover; height: 640px; max-height: 100%; position: relative; text-align: center; font-size: 0; z-index: 1; }
.section:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.section:after { content: ""; background: url("../img/section-bg.png") no-repeat top left; background-size: cover; position: absolute; left: 0; top: 0; right: 0; bottom: 0; z-index: -1; }
.section__in { text-align: left; margin: 0 auto; width: 100%; max-width: 1200px; display: inline-block; vertical-align: middle; *zoom: 1; }
.section__in:after { content: " "; display: table; clear: both; }
.section .box { width: 50%; }
.section_right .box { float: right; text-align: right; }
.section_right:after { transform: scaleX(-1); }

.advantage { position: relative; overflow: hidden; }
.advantage.is-orange .advantage__more { background: rgba(233, 168, 78, 0.8); }
.advantage.is-orange .advantage__text { border-color: #1c1634; }
.advantage.is-orange:hover .advantage__more { background: #e9a84e; }
.advantage__img { width: 100%; height: 0; padding-bottom: 100%; }
.advantage__img img { width: 100%; height: auto; }
.advantage__more { position: absolute; left: 0; right: 0; top: 100%; bottom: 0; margin-top: -100px; padding: 0 35px; color: #fff; background: rgba(28, 22, 52, 0.8); transition: top 0.3s ease, background 0.3s; }
@media only screen and (max-width: 1380px) { .advantage__more { margin-top: -80px; } }
.advantage__title { height: 100px; text-align: center; font-size: 0; text-align: left; }
.advantage__title:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
@media only screen and (max-width: 1380px) { .advantage__title { height: 80px; } }
.advantage__title h3, .advantage__title .h3 { font-size: 18px; text-align: left; display: inline-block; vertical-align: middle; }
.advantage__text { border-top: 1px solid #e9a84e; padding-top: 38px; font-size: 14px; line-height: 1.8; }
@media only screen and (max-width: 1380px) { .advantage__text { padding-top: 20px; line-height: 1.4; } }
.advantage:hover .advantage__more { top: 100px; background: #1c1634; }
@media only screen and (max-width: 1380px) { .advantage:hover .advantage__more { top: 80px; } }

.item { position: relative; }
.item__img { width: 100%; overflow: hidden; height: 0; padding-bottom: 61%; }
.item__img img { width: 100%; height: auto; }
.item__more { visibility: hidden; opacity: 0; position: absolute; left: 0; right: 0; top: 0; bottom: 0; text-align: center; font-size: 0; background: rgba(0, 0, 0, 0.8); transition: opacity 0.3s, visiblity 0.3s; }
.item__more:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.item__more-in { display: inline-block; vertical-align: middle; font-style: 18px; }
.item__more-in > a { color: #e9a84e; font-size: 18px; }
.item:hover .item__more { visibility: visible; opacity: 1; }

.block { margin-bottom: 34px; }
.block__img { width: 100%; height: 0; padding-bottom: 100%; overflow: hidden; text-align: center; background: #fff; text-align: center; font-size: 0; }
.block__img:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.block__img img { display: inline-block; vertical-align: middle; max-width: 100%; height: auto; }
.block__bottom { text-align: center; padding: 30px 10px 40px; transition: background 0.3s; border-top: 1px solid #e7e7e7; }
.block__bottom .btn { transition: background 0.3s; width: 220px; max-width: 100%; }
.block__size { font-size: 36px; color: #fff; line-height: 1; margin-bottom: 20px; transition: color 0.3s; }
.block__title { color: #e9a84e; transition: color 0.3s; font-weight: 100; }
.block:hover .block__bottom { background: #fff; }
.block:hover .block__bottom .btn { background: #1c1634; }
.block:hover .block__size, .block:hover .block__title { color: #190e2d; }

#map { height: 500px; }

.map { position: relative; }
.map__header { position: absolute; left: 0; right: 0; top: 0; background: rgba(233, 168, 78, 0.8); z-index: 10; }
.map__header h1, .map__header .h1 { color: #fff; letter-spacing: 3px; margin: 25px 0; }

.footer { border-top: 1px solid #e7e7e7; padding-bottom: 70px; text-align: left; }
.footer > .row { padding-top: 70px; }
.footer .logo-woolim { position: relative; top: -35px; }
.footer h4, .footer .h4 { text-align: left; margin-bottom: 20px; }
.footer p { font-size: 14px; color: #fff; }
.footer__logo { color: #e9a84e; font-size: 93px; margin-bottom: 20px; }
.footer__text { padding-right: 44.02516%; }
.footer__text p { margin-bottom: 20px; }
.footer h1, .footer .h1 { margin-top: 0; text-align: left; }
.footer__phone a { color: #fff; }
.footer__phone i, .footer__phone span { display: inline-block; vertical-align: middle; }
.footer__phone span { font-size: 24px; font-weight: 600; }
.footer__phone .icon-phone { font-size: 30px; margin-right: 10px; }


